.signup-form-box {
    background: rgba(10, 5, 30, 0.658); 
    border-radius: 16px;
    padding: clamp(25px, 5vw, 40px);
    width: 60%; /* í™”ë©´ ë„ˆë¹„ì— ëŒ€í•œ ë¹„ìœ¨ì„ 90%ë¡œ ìˆ˜ì • (ì›ë˜ 65%ì—ì„œ ë³€ê²½) */
    height: 100%; /* ë¶€ëª¨ ì»¨í…Œì´ë„ˆ(.signup-page-container)ì˜ ë†’ì´ë¥¼ ë”°ë¦„ */
    display: flex;
