}

.signup-form-box h2 {
  text-align: center;
  color: #9a9aff;
  margin-bottom: 30px;
  font-size: 2rem;
  text-shadow: 0 0 6px #7b7bff;
}

/* í¼ ê·¸ë£¹ (Login.cssì™€ ë™ì¼í•œ ìŠ¤íƒ€ì¼ë¡œ í†µì¼) */
.form-group {
  margin-bottom: 30px;
}

.signup-form-box form {
    flex: 1; 
    display: flex;
    flex-direction: column;
    justify-content: space-between; 
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: #c9c9ff;
}

.form-group input {
  width: 100%;
  padding: 12px;
  border: 1px solid rgba(123, 123, 255, 0.3);
  border-radius: 8px;
  background: rgba(0, 0, 10, 0.7);
  color: #e0e0f8;
  font-size: 1rem;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.form-group input:focus {
  border-color: #7b7bff;
  box-shadow: 0 0 8px rgba(123, 123, 255, 0.8);
  outline: none;
}

.signup-button {
  width: 100%;
  padding: 14px;
  background: linear-gradient(45deg, #4d4dff, #7b7bff);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
  margin-top: 20%;
  transition: background-color 0.3s, transform 0.2s, box-shadow 0.3s;
  box-shadow: 0 4px 15px rgba(77, 77, 255, 0.4);
  flex-shrink: 0;
}

.signup-button:hover {
  background: linear-gradient(45deg, #6f6fff, #9a9aff);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(77, 77, 255, 0.6);
}

.login-link {
  text-align: center;
  margin-top: 25px;
  font-size: 0.9rem;
  color: #a0a0c8;
}

.login-link a {
  color: #7b7bff;
  text-decoration: none;
  font-weight: bold;
  margin-left: 5px;
  transition: color 0.2s;
}

.login-link a:hover {
  color: #ffffff;
  text-decoration: underline;
}

/* ì‘ì€ í™”ë©´ (ëª¨ë°”ì¼) ëŒ€ì‘ */
@media (max-width: 600px) {
  .signup-form-box {
    padding: 30px 25px;
  }
}

.signup-button:active {
    transform: translateY(0);
    box-shadow: 0 2px 10px rgba(77, 77, 255, 0.8);
}

.login-link a:active {
color: #c9c9ff;
text-decoration: underline;
}

