  border-radius: 8px;
  background: rgba(0, 0, 10, 0.7);
  color: #e0e0f8;
  font-size: 1rem;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.form-group select:focus {
  border-color: #7b7bff;
  box-shadow: 0 0 8px rgba(123, 123, 255, 0.8);
  outline: none;
}

